      }
      .contrat_con {
        .other_things {
          width: 560px;
          height: 20px;
          &:nth-of-type(2), &:nth-of-type(3), &:nth-of-type(4) {
            // margin-left: 116px;
            width: 676px;
            display: none;
          }
          // height: 60px;
        }
        .first {
          .choose {
            padding-right: 0.3rem;
            // line-height: 0.3rem;
            font-size: 0.13rem;
            &:first-of-type {
              padding-left: 0.2rem;
            }
          }
        }
        .second {
          .strip_content {
            text-indent: 0.2rem;
          }
        }
        .fourth {
          div.indent {
            padding-left: 0.2rem;
            p {
              padding-left: 0.15rem;
            }
          }
          .different_type {
            font-weight: 700;
            font-size: 0.14rem;
          }
          .remark {
            p {
              padding-left: 0.1rem;
            }
          }
          .strip_content {
            .service {
              .ser_tit {
                font-weight: 700;
              }
            }
          }
        }
        .strip {
          padding-top: 0.1rem;
          .strip_title {
            font-size: 0.16rem;
            font-weight: 600;
            padding-bottom: 0.1rem;
          }
          .strip_content {
            .t {
              font-size: 0.14rem;
              font-weight: 700;
              line-height: 0.3rem;
            }
          }
          .cash {
            text-decoration: underline;
            color: #000;
            font-weight: bolder;
            padding: 0 0.04rem;
          }
        }
      }
      .confirm {
        overflow: hidden;
        margin: 0.2rem 0 0.4rem;
        padding-left: 0.2rem;
        .l {
          float: left;
          width: 3rem;
          div {
            line-height: 0.5rem;
          }
        }
        .second_party {
          float: right;
        }
      }
      h2 {
        height: 0.26rem;
        padding: 0.34rem 0;
        width: 100%;
        text-align: center;
        font-weight: normal;
        font-size: 0.18rem;
      }
    }
    .is_paper {
      padding-top: 0.2rem;
      .default {
        position: relative;
        padding: 0;
        cursor: pointer;
        z-index: 10;
        background: #eaeaea;
        width: 14px;
        height: 14px;
        display: inline-block;
        border: 1px solid #eaeaea;
        border-radius: 50%;
        margin-right: 6px;
        top: 3px;
        &:hover {
          border: 1px solid #3cc8b4;
        }
        input {
          position: relative;
          top: -3px;
          left: 1px;
          opacity: 0;
          cursor: pointer;
        }
        .radio_checked {
          width: 14px;
          height: 14px;
          position: absolute;
          z-index: 99;
          left: -1px;
          border-radius: 50%;
        }
        .choose_radio:checked + .radio_checked {
          top: -1px;
          background: #eaeaea;
          border: 1px solid #eaeaea;
          cursor: pointer;
          &:hover {
            border: 1px solid #3cc8b4;
          }
        }
        .choose_radio:checked + .radio_checked::after {
          position: absolute;
          content: "";
          width: 8px;
          height: 8px;
          top: 3px;
          left: 3px;
          border-radius: 50%;
          background: #3cc8b4;
        }
      }
      span {
        padding-right: 0.2rem;
      }
      // input {
      //   margin-right: 0.1rem;
      // }
    }
  }
  .btn {
    width: 100%;
    display: block;
    height: 0.7rem;
    .back {
      font-size: 0.14rem;
      padding: 0.4rem 0 0 0.2rem;
      height: 0.33rem;
      float: left;
      display: block;
      width: 0.8rem;
      cursor: pointer;
      color: #000;
      img {
        width: 0.2rem;
        position: relative;
        left: 0;
        top: 4px;
        margin-right: 10px;
      }
    }
    .link {
      width: 1.5rem;
      display: block;
      height: 0.7rem;
      float: right;
      button {
        display: block;
        margin-top: 0.37rem;
        width: 1.5rem;
        height: 0.4rem;
        border: 0;
        line-height: 0.4rem;
        background-color: #3cc8b4;
        color: rgba(255, 255, 255, 1);
        font-size: 0.16rem;
        text-align: center;
        font-family: Roboto;
        border-radius: 0.03rem;
        cursor: pointer;
        outline: none;
        transition: 0.3s;
        &:hover {
          background: #39beab;
        }
      }
    }
  }
}
</style>



